"use client";
import React, { useState, useRef, useEffect } from "react";
import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import interactionPlugin from "@fullcalendar/interaction";
import { EventInput, DateSelectArg, EventClickArg, EventContentArg } from "@fullcalendar/core";
import { useModal } from "@/hooks/useModal";
import { Modal } from "@/components/ui/modal";

// Leave types and colors
const leaveTypes: Record<string, string> = {
    "Sick Leave": "danger",
    "Planned Leave": "success",
    "Optional Holiday": "primary",
    "Work From Home": "warning",
};

// Pending leave counts
const pendingLeaves = {
    "Sick Leave": 6,
    "Planned Leave": 12,
    "Optional Holiday": 2,
};

interface CalendarEvent extends EventInput {
    extendedProps: {
        leaveType: string;
        reason?: string;
    };
}

const LeaveCalendar: React.FC = () => {
    const [events, setEvents] = useState<CalendarEvent[]>([]);
    const [selectedEvent, setSelectedEvent] = useState<CalendarEvent | null>(null);
    const [leaveType, setLeaveType] = useState("");
    const [startDate, setStartDate] = useState("");
    const [endDate, setEndDate] = useState("");
    const [reason, setReason] = useState("");
    const { isOpen, openModal, closeModal } = useModal();
    const calendarRef = useRef<FullCalendar>(null);

    // Convert inclusive end to exclusive for FullCalendar
    const toExclusiveEnd = (inclusiveEnd: string) => {
        const d = new Date(inclusiveEnd + "T00:00:00");
        d.setDate(d.getDate() + 1);
        return d.toISOString().split("T")[0];
    };

    const handleDateSelect = (info: DateSelectArg) => {
        resetForm();
        setStartDate(info.startStr);
        setEndDate(info.startStr);
        openModal();
    };

    const handleEventClick = (info: EventClickArg) => {
        const evt = info.event as any;
        setSelectedEvent(evt);
        setLeaveType(evt.extendedProps.leaveType);
        setReason(evt.extendedProps.reason || "");
        setStartDate(evt.startStr);
        setEndDate(evt.endStr ? toExclusiveEnd(evt.endStr) : evt.startStr);
        openModal();
    };

    const resetForm = () => {
        setSelectedEvent(null);
        setLeaveType("");
        setStartDate("");
        setEndDate("");
        setReason("");
    };

    const handleSave = () => {
        if (!leaveType || !startDate || !endDate) {
            alert("Leave type, start date and end date are mandatory");
            return;
        }

        const exclusiveEnd = toExclusiveEnd(endDate);

        if (selectedEvent) {
            setEvents((prev) =>
                prev.map((e) =>
                    e.id === selectedEvent.id
                        ? { ...e, title: leaveType, start: startDate, end: exclusiveEnd, allDay: true, extendedProps: { leaveType, reason } }
                        : e
                )
            );
        } else {
            setEvents((prev) => [
                ...prev,
                { id: Date.now().toString(), title: leaveType, start: startDate, end: exclusiveEnd, allDay: true, extendedProps: { leaveType, reason } },
            ]);
        }

        closeModal();
        resetForm();
    };

    const renderEventContent = (eventInfo: EventContentArg) => {
        const colorClass = `fc-bg-${leaveTypes[eventInfo.event.extendedProps.leaveType] || "primary"}`;
        return (
            <div className={`event-fc-color flex fc-event-main ${colorClass} p-1 rounded-sm`}>
                <div className="fc-daygrid-event-dot"></div>
                <div className="fc-event-time">{eventInfo.timeText}</div>
                <div className="fc-event-title">{eventInfo.event.title}</div>
            </div>
        );
    };

    return (
        <div>
            {/* --- Leave Summary Cards --- */}
            <div className="flex flex-wrap gap-4 mb-6">
                {Object.entries(pendingLeaves).map(([key, count]) => (
                    <div key={key} className="flex-1 min-w-[150px] bg-white dark:bg-gray-900 rounded-lg border border-gray-200 dark:border-gray-700 p-4 shadow-sm">
                        <div className="flex items-center justify-between">
                            <span className="text-gray-600 dark:text-gray-400 font-medium">{key}</span>
                            <span className="text-lg font-bold text-gray-800 dark:text-white">{count}</span>
                        </div>
                    </div>
                ))}
            </div>

            {/* --- Calendar --- */}
            <div className="rounded-2xl border border-gray-200 bg-white dark:border-gray-800 dark:bg-white/[0.03]">
                <FullCalendar
                    ref={calendarRef}
                    plugins={[dayGridPlugin, interactionPlugin]}
                    initialView="dayGridMonth"
                    headerToolbar={{ left: "prev,next addEventButton", center: "title", right: "dayGridMonth,timeGridWeek,timeGridDay" }}
                    events={events}
                    selectable={true}
                    select={handleDateSelect}
                    eventClick={handleEventClick}
                    eventContent={renderEventContent}
                    customButtons={{ addEventButton: { text: "Add Leave +", click: openModal } }}
                />
            </div>

            {/* --- Modal --- */}
            <Modal isOpen={isOpen} onClose={closeModal} className="max-w-[700px] p-6 lg:p-10">
                <div className="flex flex-col gap-4">
                    <h2 className="text-xl font-semibold text-gray-800 dark:text-white">{selectedEvent ? "Edit Leave" : "Add Leave"}</h2>

                    <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-400">Leave Type *</label>
                        <select
                            value={leaveType}
                            onChange={(e) => setLeaveType(e.target.value)}
                            className="w-full rounded-lg border border-gray-300 bg-transparent px-3 py-2 dark:border-gray-700 dark:bg-gray-900 dark:text-white"
                        >
                            <option value="">Select leave type</option>
                            {Object.keys(leaveTypes).map((type) => (
                                <option key={type} value={type}>{type}</option>
                            ))}
                        </select>
                    </div>

                    <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-400">Start Date *</label>
                        <input type="date" value={startDate} onChange={(e) => setStartDate(e.target.value)} className="w-full rounded-lg border border-gray-300 px-3 py-2 dark:border-gray-700 dark:bg-gray-900 dark:text-white" />
                    </div>

                    <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-400">End Date *</label>
                        <input type="date" value={endDate} onChange={(e) => setEndDate(e.target.value)} className="w-full rounded-lg border border-gray-300 px-3 py-2 dark:border-gray-700 dark:bg-gray-900 dark:text-white" />
                    </div>

                    <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-400">Reason</label>
                        <textarea value={reason} onChange={(e) => setReason(e.target.value)} className="w-full rounded-lg border border-gray-300 px-3 py-2 dark:border-gray-700 dark:bg-gray-900 dark:text-white" />
                    </div>

                    <div className="flex justify-end gap-3 mt-4">
                        <button onClick={closeModal} className="px-4 py-2 border rounded-lg border-gray-300 dark:border-gray-700 dark:text-white">Close</button>
                        <button onClick={handleSave} className="px-4 py-2 bg-brand-500 text-white rounded-lg">Save</button>
                    </div>
                </div>
            </Modal>
        </div>
    );
};

export default LeaveCalendar;
